/* prettier-ignore */
@use 'sass:math';
@import 'colors';
$variable-prefix: 'falcon-';
// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.
// scss-docs-start container-max-widths
$container-max-widths: () !default;
$container-max-widths: map-merge(
  (
    sm: 540px,
    md: 720px,
    lg: 960px,
    xl: 1140px,
    xxl: 1480px,
  ),
  $container-max-widths
);

$grid-breakpoints: () !default;
$grid-breakpoints: map-merge(
  (
    xs: 0,
    sm: 576px,
    md: 768px,
    lg: 992px,
    xl: 1200px,
    xxl: 1540px,
  ),
  $grid-breakpoints
);

//*-----------------------------------------------
//|   Gutter
//-----------------------------------------------*/
$grid-gutter-width: 2rem !default;



// Customize the light and dark text colors for use in our color contrast function.
$color-contrast-dark: $gray-800 !default;

// Min contrast ratio
$min-contrast-ratio: 2 !default;

//*-----------------------------------------------
//|   Brand colors
//-----------------------------------------------*/
$linkedin: #0077b5 !default;
$facebook: #3c5a99 !default;
$twitter: #1da1f2 !default;
$google-plus: #dd4b39 !default;
$github: #333333 !default;
$youtube: #ff0001 !default;


$brand-colors: () !default;
$brand-colors: map-merge(
  (
    'facebook': $facebook,
    'google-plus': $google-plus,
    'twitter': $twitter,
    'linkedin': $linkedin,
    'youtube': $youtube,
    'github': $github,
  ),
  $brand-colors
);

//*-----------------------------------------------
//|   Border
//-----------------------------------------------*/
$border-color: var(--#{$variable-prefix}border-color);
$border-width: 1px !default;

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-shadows: true !default;
$enable-gradients: false !default;
$enable-negative-margins: true !default;


// $component-active-color: var(--#{$variable-prefix}white) !default;
// $component-active-bg: var(--#{$variable-prefix}primary) !default;

//*-----------------------------------------------
//|   Shadow
//-----------------------------------------------*/
$box-shadow: var(--#{$variable-prefix}box-shadow) !default;
$box-shadow-sm: var(--#{$variable-prefix}box-shadow-sm) !default;
$box-shadow-lg: var(--#{$variable-prefix}box-shadow-lg) !default;
$box-shadow-inset: var(--#{$variable-prefix}box-shadow-inset) !default;

//*-----------------------------------------------
//|   Spacing
//----------------------------------------------

$spacer: 1rem !default;
$spacers: () !default;

$spacers: map-merge(
  (
    0: 0,
    1: $spacer * 0.25,
    2: $spacer * 0.5,
    3: $spacer,
    4: $spacer * 1.8,
    5: $spacer * 3,
    6: $spacer * 4,
    7: $spacer * 5,
    8: $spacer * 7.5,
    9: $spacer * 10,
    10: $spacer * 12.5,
    11: $spacer * 15
  ),
  $spacers
);

//*-----------------------------------------------
//|   Body
//-----------------------------------------------*/
$body-bg: $gray-200 !default;
$body-color: $gray-700 !default;

// $body-bg: var(--#{$variable-prefix}body-bg) !default;
// $body-color: var(--#{$variable-prefix}body-color) !default;

//*-----------------------------------------------
//|   Link
//-----------------------------------------------*/
$link-decoration: none !default;
$link-hover-decoration: underline !default;

//*-----------------------------------------------
//|   Components
//-----------------------------------------------*/
$border-radius: 0.25rem !default;
$border-radius-lg: 0.375rem !default;

//*-----------------------------------------------
//|   Fonts Families
//-----------------------------------------------*/
$font-family-sans-serif: 'Poppins', -apple-system, BlinkMacSystemFont,
  'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji',
  'Segoe UI Emoji', 'Segoe UI Symbol' !default;
$font-family-monospace: 'SFMono-Regular', Menlo, Monaco, Consolas,
  'Liberation Mono', 'Courier New', monospace !default;
$font-family-base: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
  Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji',
  'Segoe UI Emoji', 'Segoe UI Symbol' !default;

//*-----------------------------------------------
//|   Fonts
//-----------------------------------------------*/
$type-scale: 1.2;
$font-size-base: 1rem !default;
$font-sizes: () !default;
$font-sizes: map-merge(
  (
    '-2': math.div(1 , pow($type-scale, 2)) * $font-size-base,//11.11
    '-1': math.div(1 , $type-scale) * $font-size-base,//13.33
    0: $font-size-base,//16
    1: pow($type-scale, 1) * $font-size-base, // 19.2
    2: pow($type-scale, 2) * $font-size-base, // 23.04
    3: pow($type-scale, 3) * $font-size-base, // 27.65
    4: pow($type-scale, 4) * $font-size-base, // 33.18
    5: pow($type-scale, 5) * $font-size-base, // 39.81
    6: pow($type-scale, 6) * $font-size-base, // 47.78
    7: pow($type-scale, 7) * $font-size-base, // 57.33
    8: pow($type-scale, 8) * $font-size-base, // 68.79
  ),
  $font-sizes
);

$font-size-sm: $font-size-base * 0.875 !default;
$font-size-lg: $font-size-base * 1.2 !default;
$font-weight-thin: 100 !default;
$font-weight-lighter: 200 !default;
$font-weight-light: 300 !default;
$font-weight-normal: 400 !default;
$font-weight-medium: 500 !default;
$font-weight-semi-bold: 600 !default;
$font-weight-bold: 700 !default;
$font-weight-bolder: 800 !default;
$font-weight-black: 900 !default;

$h1-font-size: map_get($font-sizes, 5) !default;
$h2-font-size: map_get($font-sizes, 4) !default;
$h3-font-size: map_get($font-sizes, 3) !default;
$h4-font-size: map_get($font-sizes, 2) !default;
$h5-font-size: map_get($font-sizes, 1) !default;
$h6-font-size: map_get($font-sizes, '-1') !default;

$headings-font-family: $font-family-sans-serif !default;
$headings-font-weight: $font-weight-medium !default;
$headings-color: var(--#{$variable-prefix}headings-color) !default;

// scss-docs-start display-headings
$display-font-sizes: () !default;
$display-font-sizes: map-merge(
  (
    1: map_get($font-sizes, 8),
    2: map_get($font-sizes, 7),
    3: map_get($font-sizes, 6),
    4: map_get($font-sizes, 5),
    5: map_get($font-sizes, 4),
    6: map_get($font-sizes, 3),
  ),
  $display-font-sizes
);

$display-font-weight: $font-weight-black !default;
$display-line-height: 1 !default;
// scss-docs-end display-headings

$lead-font-size: $font-size-lg !default;
$lead-font-weight: $font-weight-normal !default;

$small-font-size: 75% !default;

$text-muted: $gray-500 !default;

$blockquote-font-size: $font-size-lg !default;

//*-----------------------------------------------
//|   Accordion
//-----------------------------------------------*/
$accordion-border-color: var(--#{$variable-prefix}accordion-border-color) !default;
$accordion-button-active-color: var(--#{$variable-prefix}accordion-button-active-color) !default;
$accordion-button-active-bg: var(--#{$variable-prefix}accordion-button-active-bg) !default;

//*-----------------------------------------------
//|   Tables
//-----------------------------------------------*/
$table-border-color: var(--#{$variable-prefix}table-border-color) !default;
$table-head-bg: $gray-900 !default;
$table-head-color: $dark !default;
$table-dark-bg: $gray-1000 !default;
$table-dark-border-color: lighten($gray-1000, 7.5%) !default;
$table-striped-order: even !default;
// $table-accent-bg: $gray-100 !default;
$table-cell-padding-y: 0.75rem !default;
$table-cell-padding-x: 0.75rem !default;
$table-group-separator-color: inherit !default;





//*-----------------------------------------------
//|   Buttons
//-----------------------------------------------*/
$btn-falcon-box-shadow: var(--#{$variable-prefix}btn-falcon-box-shadow) !default;
$btn-falcon-hover-box-shadow: var(--#{$variable-prefix}btn-falcon-hover-box-shadow) !default;

//*-----------------------------------------------
//|   Buttons and Forms
//-----------------------------------------------*/
$input-btn-padding-y: 0.3125rem !default;
$input-btn-padding-x: 1rem !default;

$input-btn-padding-y-sm: 0.1875rem !default;
$input-btn-padding-x-sm: 0.75rem !default;

$input-btn-padding-y-lg: 0.375rem !default;
$input-btn-padding-x-lg: 1.25rem !default;

$btn-reveal-hover-shadow: 0 0 0 1px rgba(43, 45, 80, 0.1),
  0 2px 5px 0 rgba(43, 45, 80, 0.08), 0 1px 1.5px 0 rgba($black, 0.07),
  0 1px 2px 0 rgba($black, 0.08) !default;
$btn-font-weight: $font-weight-medium !default;
$btn-focus-width: 0 !default;

//*-----------------------------------------------
//|   Forms
//-----------------------------------------------*/
$input-disabled-bg: var(--#{$variable-prefix}200) !default;
$input-group-addon-bg: var(--#{$variable-prefix}200) !default;
$input-border-color: var(--#{$variable-prefix}input-border-color) !default;
$input-placeholder-color: var(--#{$variable-prefix}input-placeholder-color) !default;
$input-color: var(--#{$variable-prefix}input-color) !default;
$form-check-margin-bottom: 0.34375rem !default;
$form-label-font-size: $font-size-sm !default;
$form-check-input-bg: transparent !default;
$form-check-input-border: 1px solid var(--#{$variable-prefix}form-check-input-border-color) !default;
$form-switch-color: $gray-500 !default;
$form-select-disabled-bg: var(--#{$variable-prefix}200) !default;
$input-bg: var(--#{$variable-prefix}input-bg) !default;
$input-focus-border-color: var(--#{$variable-prefix}input-focus-border-color) !default;
$form-file-button-bg: $gray-900 !default;
$form-file-button-color: $gray-300 !default;

//*-----------------------------------------------
//|   Z-index
//-----------------------------------------------*/

$zindex-sticky: 1015 !default;

//*-----------------------------------------------
//|   Navigation
//-----------------------------------------------*/
$navbar-dark-color: var(--#{$variable-prefix}navbar-dark-color) !default;
$navbar-dark-hover-color: var(--#{$variable-prefix}navbar-dark-hover-color) !default;
$navbar-dark-active-color: var(--#{$variable-prefix}navbar-dark-active-color) !default;
$navbar-dark-disabled-color: var(--#{$variable-prefix}navbar-dark-disabled-color) !default;
$navbar-dark-toggler-border-color: var(--#{$variable-prefix}navbar-dark-toggler-border-color) !default;
$navbar-light-color: var(--#{$variable-prefix}navbar-light-color) !default;
$navbar-light-hover-color: var(--#{$variable-prefix}navbar-light-hover-color) !default;
$navbar-light-active-color: var(--#{$variable-prefix}navbar-light-active-color) !default;
$navbar-light-disabled-color: var(--#{$variable-prefix}navbar-light-disabled-color) !default;
$navbar-light-toggler-border-color: var(--#{$variable-prefix}navbar-light-toggler-border-color) !default;

$navbar-font-size: $font-size-base * 0.8 !default;

// $navbar-light-color: rgba($black, 0.55) !default;
$navbar-dark-color: rgba($white, 0.70) !default;
$navbar-light-hover-color: rgba($black, 0.9) !default;
$navbar-light-active-color: $primary !default;
$navbar-light-disabled-color: rgba($black, 0.25) !default;

// $navbar-dark-color: rgba($white, .7) !default;
$navbar-dark-hover-color: rgba($white, 0.9) !default;

$navbar-padding-y: map_get($spacers, 2) !default;
$navbar-padding-x: $spacer !default;
$top-nav-height: 4.3125rem !default;
$standard-nav-height: 3.5625rem !default;

$navbar-light-toggler-icon-bg: str-replace(
  url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='#9da9bb' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M0 6h30M0 14h30M0 22h30'/%3E%3C/svg%3E"),
  '#',
  '%23'
) !default;

$navbar-dark-toggler-icon-bg: str-replace(
  url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='#9da9bb' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M0 6h30M0 14h30M0 22h30'/%3E%3C/svg%3E"),
  '#',
  '%23'
) !default;

// Navbar Glass

$bg-navbar-glass: var(--#{$variable-prefix}bg-navbar-glass) !default;

// /*-----------------------------------------------
//|   Navbar Brand
//-----------------------------------------------*/
$navbar-brand-font-size: map_get($font-sizes, 3) !default;
$navbar-brand-font-weight: $font-weight-bolder !default;

//*-----------------------------------------------
//|   Dropdowns | Dropdown menu container and contents.
//-----------------------------------------------*/
$dropdown-bg: var(--#{$variable-prefix}dropdown-bg) !default;
$dropdown-color: var(--#{$variable-prefix}dropdown-color) !default;
$dropdown-link-color: var(--#{$variable-prefix}dropdown-link-color) !default;
$dropdown-link-hover-color: var(--#{$variable-prefix}dropdown-link-hover-color) !default;
$dropdown-link-hover-bg: var(--#{$variable-prefix}dropdown-link-hover-bg) !default;
$dropdown-link-disabled-color: var(--#{$variable-prefix}dropdown-link-disabled-color) !default;
$dropdown-divider-bg: var(--#{$variable-prefix}dropdown-border-color) !default;
$dropdown-border-color: var(--#{$variable-prefix}dropdown-border-color) !default;
$dropdown-link-active-color: var(--#{$variable-prefix}dropdown-link-active-color) !default;
$dropdown-link-active-bg: var(--#{$variable-prefix}dropdown-link-active-bg) !default;
$dropdown-box-shadow: var(--#{$variable-prefix}dropdown-box-shadow) !default;

$dropdown-font-size: $navbar-font-size !default;
$dropdown-item-padding-y: map_get($spacers, 1) !default;
$dropdown-item-padding-x: map_get($spacers, 3) !default;
$dropdown-padding-y: map_get($spacers, 3) !default;


//*-----------------------------------------------
//|   Pagination
//-----------------------------------------------*/
$pagination-padding-y: 0.5rem !default;
$pagination-padding-x: 0.75rem !default;
$pagination-padding-y-sm: 0.25rem !default;
$pagination-padding-x-sm: 0.5rem !default;
$pagination-padding-y-lg: 0.75rem !default;
$pagination-padding-x-lg: 1.5rem !default;

$pagination-color: var(--#{$variable-prefix}pagination-color) !default;
$pagination-bg: var(--#{$variable-prefix}pagination-bg) !default;
$pagination-border-color: var(--#{$variable-prefix}pagination-border-color) !default;
$pagination-focus-color: var(--#{$variable-prefix}pagination-focus-color) !default;
$pagination-focus-bg: var(--#{$variable-prefix}pagination-focus-bg) !default;
$pagination-focus-box-shadow: var(--#{$variable-prefix}pagination-focus-box-shadow) !default;
$pagination-hover-color: var(--#{$variable-prefix}pagination-hover-color) !default;
$pagination-hover-bg: var(--#{$variable-prefix}pagination-hover-bg) !default;
$pagination-hover-border-color: var(--#{$variable-prefix}pagination-hover-border-color) !default;
$pagination-active-color: var(--#{$variable-prefix}pagination-active-color) !default;
$pagination-active-bg: var(--#{$variable-prefix}pagination-active-bg) !default;
$pagination-active-border-color: var(--#{$variable-prefix}pagination-active-border-color) !default;
$pagination-disabled-color: var(--#{$variable-prefix}pagination-disabled-color) !default;
$pagination-disabled-bg: var(--#{$variable-prefix}pagination-disabled-bg) !default;
$pagination-disabled-border-color: var(--#{$variable-prefix}pagination-disabled-border-color) !default;

//*-----------------------------------------------
//|   Cards
//-----------------------------------------------*/
// $card-spacer-y: map_get($spacers, 3) !default;
$card-spacer-y: 1.25rem !default;
$card-spacer-x: 1.25rem !default;
$card-border-width: 0px !default;
$card-border-radius: $border-radius-lg !default;
$card-border-color: var(--#{$variable-prefix}card-border-color) !default;
$card-cap-bg: var(--#{$variable-prefix}card-cap-bg) !default;
$card-cap-padding-y: $spacer !default;
$card-cap-padding-x: 1.25rem !default;
$card-bg: var(--#{$variable-prefix}card-bg);

// /*-----------------------------------------------
//|   Tooltip
//-----------------------------------------------*/

$tooltip-padding-y: 0.5rem !default;
$tooltip-font-size: map_get($font-sizes, '-1') !default;

//*-----------------------------------------------
//|   Badge
//-----------------------------------------------*/
$badge-font-weight: $font-weight-semi-bold !default;
$badge-padding-y: 0.355555em !default;
$badge-padding-x: 0.711111em !default;

// /*-----------------------------------------------
//|   Modal
//-----------------------------------------------*/
$modal-content-border-radius: $border-radius-lg !default;
$modal-content-bg: var(--#{$variable-prefix}modal-content-bg) !default;

///*-----------------------------------------------
//|   List Group
//-----------------------------------------------*/
$list-group-color: var(--#{$variable-prefix}800);
$list-group-bg: var(--#{$variable-prefix}list-group-bg) !default;
$list-group-border-color: var(--#{$variable-prefix}border-color) !default;
$list-group-action-hover-color: var(--#{$variable-prefix}list-group-action-hover-color) !default;
$list-group-hover-bg: var(--#{$variable-prefix}list-group-hover-bg) !default;
$list-group-action-active-bg: var(--#{$variable-prefix}200);

//*-----------------------------------------------
//|   Thumbnail
//-----------------------------------------------*/
$thumbnail-bg: var(--#{$variable-prefix}thumbnail-bg) !default;
$thumbnail-border-width: 3px !default;
$thumbnail-border-color: var(--#{$variable-prefix}thumbnail-bg) !default;

// /*-----------------------------------------------
//|   Breadcrumbs
//-----------------------------------------------*/
$breadcrumb-padding-x: 0 !default;
$breadcrumb-margin-bottom: 0 !default;
$breadcrumb-bg: 'transparent';
$breadcrumb-divider: quote('/') !default;

//*-----------------------------------------------
//|   Carousel
//-----------------------------------------------*/
$carousel-transition-duration: 0.8s !default;

// /*-----------------------------------------------
//|   Spinner
//-----------------------------------------------*/
$spinner-width-sm: 1.35rem !default;
$spinner-height-sm: $spinner-width-sm !default;

// /*-----------------------------------------------
//|   Spinner
//-----------------------------------------------*/
$hr-color: $border-color !default;
$hr-opacity: 1 !default;


//*-----------------------------------------------
//|   Treeview
//----------------------------------------------

$treeview-transition-collapse: height .15s ease !default;
$treeview-row-bg-odd : var(--#{$variable-prefix}200) !default;
$treeview-row-bg-even : var(--#{$variable-prefix}100) !default;
$treeview-text-color : var(--#{$variable-prefix}600) !default;

//*=============================================
//|   Falcon Specific
//=============================================


//*-----------------------------------------------
//|   Viewport Heights & Widths
//----------------------------------------------

$viewport-heights: () !default;
$viewport-heights: map-merge(
  (
    25: 25vh,
    50: 50vh,
    75: 75vh,
    100: 100vh,
  ),
  $viewport-heights
);

$viewport-widths: () !default;
$viewport-widths: map-merge(
  (
    25: 25vw,
    50: 50vw,
    75: 75vw,
    100: 100vw,
  ),
  $viewport-widths
);

$sizes: () !default;
$sizes: map-merge(
  (
    25: 25%,
    50: 50%,
    75: 75%,
    100: 100%,
    auto: auto,
  ),
  $sizes
);

// /*-----------------------------------------------
//|   Calendar
//-----------------------------------------------*/
$calendar-color: $danger !default;

// /*-----------------------------------------------
//|   Footer
//-----------------------------------------------*/

$footer-height: 3.9875rem !default;
$responsive-footer-height: 0.625rem !default;

// /*-----------------------------------------------
//|   Avatar
//-----------------------------------------------*/

$avatars-dimension: () !default;
$avatars-dimension: map-merge(
  (
    's': toRem(20rem),
    'm': toRem(24rem),
    'l': toRem(28rem),
    'xl': toRem(32rem),
    '2xl': toRem(40rem),
    '3xl': toRem(56rem),
    '4xl': toRem(98rem),
    '5xl': toRem(168rem),
  ),
  $avatars-dimension
);

$gutters: () !default;
$gutters: map-merge($spacers, (card: $card-spacer-x * 2));

//*-----------------------------------------------
//|   Plugins color variables
//-----------------------------------------------*/

$data-table-pagination-button-color: $black !default;
// $emojipicker-icon: 'data:image/png;base64,' !default;

//*-----------------------------------------------
//|   Navbar Vertical
//-----------------------------------------------*/

$navbar-vertical-breakpoints: mapReverse($grid-breakpoints) !default;
$navbar-vertical-width: 12.625rem !default;
$navbar-vertical-icon-width: 1.5rem !default;
$navbar-vertical-collapsed-width: 3.125rem !default;
$navbar-vertical-hover-width: 12.625rem !default;
$navbar-vertical-collapsed-hover-shadow: 0.625rem 0 0.625rem -0.5625rem var(--#{$variable-prefix}navbar-vertical-collapsed-hover-shadow-color) !default;
$navbar-vertical-variation-width: 14.5rem !default;
$navbar-vertical-variation-collapsed-width: 4.125rem !default;

$navbar-vertical-link-font-size: $font-size-sm !default;
$navbar-vertical-dropdown-font-size: 0.8125rem !default;
$navbar-vertical-bg-color: var(--#{$variable-prefix}navbar-vertical-bg-color) !default;
$navbar-vertical-link-color: var(--#{$variable-prefix}navbar-vertical-link-color) !default;
$navbar-vertical-link-hover-color: var(--#{$variable-prefix}navbar-vertical-link-hover-color) !default;
$navbar-vertical-link-active-color: var(--#{$variable-prefix}navbar-vertical-link-active-color) !default;
$navbar-vertical-link-disable-color: var(--#{$variable-prefix}navbar-vertical-link-disable-color) !default;
$navbar-vertical-hr-color: var(--#{$variable-prefix}navbar-vertical-hr-color) !default;
$navbar-vertical-scrollbar-color: var(--#{$variable-prefix}navbar-vertical-scrollbar-color) !default;

$navbar-light-brand-color: var(--#{$variable-prefix}navbar-light-brand-color) !default;
$navbar-light-brand-hover-color: $navbar-light-brand-color !default;
$navbar-dark-brand-color: var(--#{$variable-prefix}navbar-dark-brand-color) !default;
$navbar-dark-brand-hover-color: $navbar-dark-brand-color !default;
$navbar-vertical-label-color: var(--#{$variable-prefix}500) !default;
//*-----------------------------------------------
//|   Navbar Inverted
//-----------------------------------------------*/
$navbar-inverted-bg-color: $gray-1000 !default;
$navbar-inverted-link-color: $gray-500 !default;
$navbar-inverted-link-hover-color: $gray-200 !default;
$navbar-inverted-link-active-color: $navbar-inverted-link-hover-color !default;
$navbar-inverted-link-disable-color: $gray-700 !default;
$navbar-inverted-hr-color: rgba($white, 0.08) !default;
$navbar-inverted-scrollbar-color: $gray-400 !default;
$navbar-inverted-label-color: $gray-700 !default;

//*-----------------------------------------------
//|   Navbar Vibrant
//-----------------------------------------------*/
$navbar-vibrant-bg-image: linear-gradient(
    -45deg,
    rgba(0, 160, 255, 0.86),
    #0048a2
  ),
  url(../img/generic/bg-navbar.png) !default;
$navbar-vibrant-link-color: rgba($white, 0.75) !default;
$navbar-vibrant-link-hover-color: $white !default;
$navbar-vibrant-link-active-color: $navbar-vibrant-link-hover-color !default;
$navbar-vibrant-link-disable-color: rgba($white, 0.45) !default;
$navbar-vibrant-hr-color: rgba($white, 0.2) !default;
$navbar-vibrant-scrollbar-color: $gray-400 !default;
$navbar-vibrant-label-color: rgba($white, 0.4) !default;

//*-----------------------------------------------
//|   Navbar card
//-----------------------------------------------*/
$navbar-card-bg-color: $white !default;
$navbar-card-link-color: $gray-700 !default;
$navbar-card-link-hover-color: $gray-900 !default;
$navbar-card-link-active-color: $primary !default;
$navbar-card-link-disable-color: $gray-400 !default;
$navbar-card-shadow: $box-shadow !default;
$navbar-card-hr-color:  rgba($black, 0.08) !default;
$navbar-card-scrollbar-color: rgba($gray-600, 0.3) !default;
$navbar-card-label-color: $gray-500 !default;

//*-----------------------------------------------
//|   Utilities
//-----------------------------------------------*/
$border-width: 1px !default;

//*-----------------------------------------------
//|   Popover
//-----------------------------------------------*/
$popover-bg: var(--#{$variable-prefix}popover-bg) !default;
$popover-border-color: var(--#{$variable-prefix}popover-border-color) !default;
$popover-box-shadow: var(--#{$variable-prefix}popover-box-shadow) !default;
$popover-header-bg: var(--#{$variable-prefix}popover-header-bg) !default;
$popover-header-border-bottom-color: var(--#{$variable-prefix}popover-popover-header-ottom-color) !default;
$popover-header-color: var(--#{$variable-prefix}popover-peader-color) !default;
$popover-body-color: var(--#{$variable-prefix}popover-body-color) !default;
$popover-arrow-color: var(--#{$variable-prefix}popover-arrow-color) !default;
$popover-arrow-outer-color: var(--#{$variable-prefix}popover-arrow-outer-color) !default;

//*-----------------------------------------------
//|   Toast
//-----------------------------------------------*/
$toast-background-color: var(--#{$variable-prefix}toast-background-color) !default;
$toast-header-color: var(--#{$variable-prefix}600) !default;
$toast-header-background-color: var(--#{$variable-prefix}toast-background-color) !default;

//*-----------------------------------------------
//|   Toast
//-----------------------------------------------*/
$progress-bg: var(--#{$variable-prefix}200) !default;

//*-----------------------------------------------
//|   Scrollbar
//-----------------------------------------------*/
$scrollbar-bg: var(--#{$variable-prefix}scrollbar-bg) !default;
$simplebar-bg: $scrollbar-bg !default;
